/**
*  Created by   
*  On  2018/8/21
*  Content
*/
<template>
  <div class='identity-details'>
    <el-row style="margin-bottom: 30px;">
      <el-col :span="4">
        <el-button size="small" type="primary" plain @click="$router.go(-1)"></el-button>
      </el-col>
      <el-col :span="16" style="text-align: right;">
        <el-button class="refuse-btn" type="danger" :disabled="details.antiMoneyAudit == 0" size="small"
                   @click.native="modifyState()">
        </el-button>
        <el-button class="success-btn" type="success" :disabled="details.antiMoneyAudit == 1" size="small"
                   @click.native="modifyState()">
        </el-button>
        <el-button size="small" type="primary" @click.native="fixContent"></el-button>
      </el-col>
    </el-row>
    <div class="content" >
      <el-row :gutter="24">
        <el-col :span="8"><span></span><span>{{details.phone}}</span></el-col>
        <el-col :span="8"><span></span><span>{{details.realName}}</span></el-col>
        <el-col :span="8"><span></span><span>{{details.cardNo}}</span></el-col>
      </el-row>
      <el-row :gutter="24" style="margin-top:20px;">
        <el-col :span="8"><span></span>
          <span :class="[details.cardBackAudit ==1 || CardAudit == 1 ?'green':'red']">
            {{CardAudit == 1 ?'' : details.cardBackAudit ==-1?'':cardBackAuditArr[details.cardBackAudit]}}</span>
        </el-col>
        <el-col :span="8">
          <span></span>
          <span :class="[(details.autoVerifyDesc =='')||(details.autoVerifyDesc =='')?'green':'red']">
            {{details.autoVerifyDesc}}</span>
        </el-col>
        <el-col :span="8">
          <el-button type="warning" size="small" class="check-idenfity" @click.native="checkThreeIdenfity">
          </el-button>
        </el-col>
      </el-row>
      <el-row :gutter="24" >
        <el-col :span="8">
          <span></span>
          <img :src="details.cardFrontUrl" alt="">
        </el-col>
        <el-col :span="8">
          <span></span> <img :src="details.cardBackUrl" alt="">
        </el-col>
        <el-col :span="8">
          <span></span> <img :src="details.antiMoneyUrl" alt="">
        </el-col>
      </el-row>
    </div>
    <span></span>
    <sac-table :data="list" v-if="list.length">
      <el-table-column prop="auditUserName" label=""></el-table-column>
      <el-table-column prop="antiMoneyAudit" label="" :formatter="formatAntiMoneyAudit"></el-table-column>
      <el-table-column prop="custServiceRemark" label=""></el-table-column>
      <el-table-column prop="updateTime" label=""></el-table-column>
    </sac-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="dialogForm" ref="dialogForm" :rules="details.antiMoneyAudit == '1'?rules:{}">
        <el-form-item prop="reason" :label="`${details.antiMoneyAudit == '0' ? '':''}`">
          <el-input type="textarea"
                    :autosize="{ minRows: 2, maxRows: 6 }"
                    v-model="dialogForm.reason" maxlength="50"
                    placeHolder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click.native="updateAuditStatus" size="small"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import PPreview from "vue-simple-picture-preview";

  export default {
    name: "identity-details",
    components: {
      PPreview
    },
    data() {
      return {
        details: {},
        cardBackAuditArr: {
          0: '',
          1: '',
          2: '',
        },
        CardAudit: '',//
        list: [],
        dialogFormVisible: false,
        dialogForm: {
          reason: '',
        },
        rules: {
          reason: [
            { required: true, message: '', trigger: 'blur' },
          ]
        }
      };
    },
    methods: {
      resetForm() {
        this.$refs.dialogForm && this.$refs.dialogForm.resetFields();
      },
      fixContent() {
        this.$router.push({
          name: 'identityModify',
          params: this.details
        })
      },
      formatAntiMoneyAudit(row, column) {
        return row.antiMoneyAudit == 0 ? '' : ''
      },
      // 
      checkThreeIdenfity() {
        this.$confirm('0.5', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.post("/wallet/backmgr/user/checkRealInfo.do", {
            userId: this.details.userId,
          }).then((res) => {
            if (res.result.verifyMsg && res.result.verifyMsg.indexOf(':') > -1) {
              this.details.autoVerifyDesc = res.result.verifyMsg.split(':')[1];
            } else {
              this.details.autoVerifyDesc = res.result.verifyMsg;
            }
            this.CardAudit = res.result.CardAudit;
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      getAuditRecdList() {
        this.$http.post("/wallet/backmgr/user/getAuditRecdList.do", {
          userId: this.details.userId,
        }).then((res) => {
          this.list = res.result.list;
        })
      },
      modifyState() {
        this.dialogFormVisible = true;
        this.resetForm();
      },
      updateAuditStatus() {
        this.$refs.dialogForm.validate((valid) => {
          if (valid) {
            const { userId, antiMoneyAudit } = this.details; // auditStatus 01  auditType 12
            const { reason } = this.dialogForm;
            this.$http.post("wallet/backmgr/user/updateAuditStatus.do", {
              userId,
              auditStatus: antiMoneyAudit == '1' ? '0' : '1',
              auditType: 1,
              reason: reason || 'empty'
            }).then((res) => {
              this.dialogFormVisible = false;
              this.$router.go(-1);
            })
          }
        })
      }
    },
    activated() {
      if (this.$route.params.userId) {
        this.resetForm();
        this.details = this.$route.params;
        this.details.autoVerifyDesc = this.details.autoVerifyDesc && this.details.autoVerifyDesc.indexOf(':') > -1 ? this.details.autoVerifyDesc.split(':')[1] : this.details.autoVerifyDesc;
        this.getAuditRecdList();
      } else {
        this.$router.go(-1);
      }
    }
  };
</script>
<style lang="less">
  .identity-details {
    .green {
      color: green;
    }
    .red {
      color: red;
    }
    img {
      max-height: 250px;
      max-width: 350px;
      margin: 20px 0;
    }
    .content {
      margin-bottom: 20px;
    }
  }
</style>



